@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Poppins', sans-serif;
}

.App {
    width: 90%;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
    color: #222;
}

main {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 30px 0;
    display: -webkit-flex;
    display: flex;
}

.page {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

aside {
    min-width: 240px;
}

footer {
    background-color: black;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
}

.single-article {
    margin-top: 40px;
}

.single-article h3 {
    text-transform: uppercase;
    font-size: 24px;
    font-weight: bold;
}
header {
    -webkit-flex-basis: 40vh;
            flex-basis: 40vh;
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    min-width: 100%;
    min-height: 100%;
}
.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}
.contact form button {
    display: block;
    padding: 5px;
    cursor: pointer;
    font-size: 20px;
    margin: 0 20% 0 auto;
}
.products {
    margin-bottom: 20px;
}

.products ul {
    list-style-type: square;
    padding-left: 30px;
}

.products ul li {
    margin: 10px;
}

.products ul li a {
    text-decoration: none;
    color: #333;
}

.products ul li a:hover {
    text-decoration: underline;
    color: black;
}
.navigation {
    padding: 0 30px 30px 0;
}

 ul {
     list-style-type: none;
}


.navigation a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: all .2s;
    position: relative;
}

.navigation a:hover {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
}

.navigation a.active {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}


.navigation li:nth-child(1) a.active {
    border-left: 10px solid green;
}
.navigation li:nth-child(2) a.active {
    border-left: 10px solid blue;
}
.navigation li:nth-child(3) a.active {
    border-left: 10px solid orange;
}
.navigation li:nth-child(4) a.active {
    border-left: 10px solid red;
}











