.products {
    margin-bottom: 20px;
}

.products ul {
    list-style-type: square;
    padding-left: 30px;
}

.products ul li {
    margin: 10px;
}

.products ul li a {
    text-decoration: none;
    color: #333;
}

.products ul li a:hover {
    text-decoration: underline;
    color: black;
}